import React, { Component } from 'react'
import './index.less'

import boy from '@/assets/img/boy.jpg'
import avatar from '@/assets/img/avatar.jpg'

export default class DemoItem extends Component {
  render() {
    return (
      <div className="demo-item">
        <div className="DemoItem">
          <div className="DemoItem-banner">
            <img src={boy} alt=""/>
          </div>
          <div className="DemoItem-info">
            <div className="DemoItem-info-title">
              <div className="DemoItem-info-title-userAvatar">
                <img src={avatar} alt=""/>
              </div>
              <div className="DemoItem-info-title-text">
                <span>用一个div实现小米新logo</span>
              </div>
            </div>
            <div className="DemoItem-info-text">
              <pre>
                function whatIsThis () &#123;<br/>
                  &nbsp;let self = this<br/>
                  &nbsp;return function &#123;<br/>
                  &nbsp;&nbsp;console.log('this is javascript code')<br/>
                  &nbsp;&#125;<br/>
                &#125;
              </pre>
            </div>
          </div>
        </div>  
      </div>
      
    )
  }
}
