import React, { Component } from 'react'
import DemoItem from '@/components/DemoItem'
import './index.less'

import avatar from '@/assets/img/home-avatar.png'
import web from '@/assets/cutImg/WEB前端.png'

export default class Home extends Component {
  render() {
    return (
      <div className="home">
        <div className="home-bg"></div>
        <div className="home-contaniner">
          {/* 首部 */}
          <div className="header">
            <div className="header-container">
              {/* 头像 */}
              <div className="userAvatar">
                <div className="userAvatar-webIcon">
                  <img src={web} alt=""/>
                </div>
                <div className="userAvatar-img">
                  <div className="userAvatar-icon"><div>,,</div></div>
                  <div className="userAvatar-img-frame">
                    <img src={avatar} alt=""/>
                  </div>
                </div>
              </div>
              {/* 个人信息 */}
              <div className="userInfo">
                <div className="userInfo-container">
                  <div className="userInfo-container-title">
                    Da-Zheng
                  </div>
                  <div className="userInfo-container-subTitle">
                    <div className="userInfo-container-subTitle-blog">Blog</div>
                    <div className="userInfo-container-subTitle-text">
                      <div className="userInfo-container-subTitle-text-little">
                        本博客由本人独立设计开发
                      </div>
                      <div className="userInfo-container-subTitle-text-button">
                        点击查看设计稿
                      </div>
                    </div>
                  </div>
                  <div className="userInfo-containere-connect">
                    <div>Phone: 176220953973</div>
                    <div>eMail: 924370430@qq.com</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="home-container-main">
            <DemoItem />
            <DemoItem />
          </div>
        </div>
      </div>
    )
  }
}
